.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* App.css */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #333;
  /* Dark gray background */
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  width: 80%;
  /* Adjust width as needed */
  max-width: 500px;
  /* Maximum width */
  height: 200px;
  /* Shorter height */
  color: white;
  /* Ensure text is readable on dark background */
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* Center content vertically */
  align-items: center;
  /* Center content horizontally */
}

.modal-content h1 {
  font-size: 2rem;
  /* Adjust font size if needed */
  margin-bottom: 10px;
  /* Adjust margin if needed */
}

.modal-content p {
  margin-bottom: 10px;
  /* Adjust margin if needed */
}

.modal-content button {
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
}

.modal-content a {
  color: white;
  /* Make the link white */
  text-decoration: underline;
  /* Optional: underline the link */
}

.comments-section {
  margin-top: 10px;
  /* Add space before the comments section */
}